.banner{
    background-size: cover;
    height: 780px;
    color: white;
}

.content{
    padding-top: 600px;
    height: 190px;
    padding-left: 50px;
}

.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
    color: white;
}

.button{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, .5);
    cursor: pointer;
    margin-right: 1rem; ;
}

.button:hover{
    color: black;
    background: #e6e6e6;
}

.banner_buttons{
    padding: 2px;
}

.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem ;
    height: 80px;
    max-width: 350px;
}

.fade_bottom{
    margin-top: 290px;
    height: 7.5rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,0.61),#111);
}